
.ant-table-content{
    table{
        width: auto !important;
        .ant-table-tbody{
            .ant-table-cell{
                padding: 0;
            } 
         }
    }
}


.my-ant-table-cell{
    padding: 12px 8px;
    &.green{
        background-color: green;
    }
    &.yellow{
        background-color: yellow;
    }
    &.red{
        background-color: red;
    }
}

.ant-select-item{
    font-size: 20px !important;
    line-height: 28px !important;
}
